import React, { Component } from 'react'
import {
  Space,
  Menu,
  Button,
  Divider,
  Row,
  Col,
  Dropdown
} from '../../core/index'
import {
  MoreOutlined,
  EditOutlined,
  DeleteOutlined,
  PushpinOutlined,
  SearchOutlined,
  SyncOutlined,
  PrinterOutlined,
  DownloadOutlined,
  FilterOutlined,
  AppstoreAddOutlined
} from '@ant-design/icons'

export default class TitleCustomize extends Component {
  render() {
    return (
      <Row justify='end'>
        <Col span={20} style={{ display: 'flex' }}>
          <Space size={0}>
            <Dropdown
              overlay={
                <Menu style={{ minWidth: '250px' }} className='dms-divder-menu'>
                  <Divider orientation='left' plain>
                    Vues système
                  </Divider>
                  {this.props.vues.vueSysteme.map((data, indes) => (
                    <Menu.Item key='1'>
                      <Row>
                        <Col span={2}>
                          <Button
                            type='text'
                            shape='circle'
                            icon={data.check && <PushpinOutlined />}
                            size={'small'}
                            // onClick={(e) => e.preventDefault()}
                          />
                        </Col>
                        <Col span={17}>
                          <Divider type='vertical' />
                          {data.titre}
                        </Col>
                        <Col span={5}>
                          <Row wrap={false} justify='end' align='middle'>
                            <Col span={24}>
                              <Space>
                                <Button
                                  type='text'
                                  shape='circle'
                                  icon={<EditOutlined />}
                                  size={'small'}
                                  // onClick={(e) => e.preventDefault()}
                                />
                                <Button
                                  type='text'
                                  shape='circle'
                                  icon={<DeleteOutlined />}
                                  size={'small'}
                                  // onClick={(e) => e.preventDefault()}
                                />
                              </Space>
                            </Col>
                          </Row>
                        </Col>
                      </Row>
                    </Menu.Item>
                  ))}

                  <Divider orientation='left' plain>
                    Vues personnalisés
                  </Divider>
                  {this.props.vues.vuesPersonnalises.map((data, indes) => (
                    <Menu.Item key='1'>
                      <Row>
                        <Col span={2}>
                          <Button
                            type='text'
                            shape='circle'
                            icon={data.check && <PushpinOutlined />}
                            size={'small'}
                            // onClick={(e) => e.preventDefault()}
                          />
                        </Col>
                        <Col span={17}>
                          <Divider type='vertical' />
                          {data.titre}
                        </Col>
                        <Col span={5}>
                          <Row wrap={false} justify='end' align='middle'>
                            <Col span={24}>
                              <Space>
                                <Button
                                  type='text'
                                  shape='circle'
                                  icon={<EditOutlined />}
                                  size={'small'}
                                  // onClick={(e) => e.preventDefault()}
                                />
                                <Button
                                  type='text'
                                  shape='circle'
                                  icon={<DeleteOutlined />}
                                  size={'small'}
                                  // onClick={(e) => e.preventDefault()}
                                />
                              </Space>
                            </Col>
                          </Row>
                        </Col>
                      </Row>
                    </Menu.Item>
                  ))}
                  <Menu.Divider />
                  <Menu.Item key='3'>
                    <div className='vue-personnalisee'>
                      Créer une vue personnalisée
                    </div>
                  </Menu.Item>
                </Menu>
              }
              trigger={['click']}
            >
              <Button
                type='text'
                shape='circle'
                icon={
                  <MoreOutlined
                    className='dms-table-icon-secandary'
                    // style={{
                    //   color: '#606060'e
                    // }}
                  />
                }
                size={'large'}
                onClick={(e) => e.preventDefault()}
              />
            </Dropdown>
            {/* <Typography.Title level={5}> */}
            <div
              className='dms-table-icon-secandary'
              style={{
                fontWeight: '500',
                fontSize: '17px'
                // color: '#444'
              }}
            >
              {' '}
              Titre du tableauh
            </div>
            {/* </Typography.Title> */}
            <Button
              type='text'
              shape='circle'
              icon={<SyncOutlined className='dms-table-icon-primary' />}
              size={'small'}
            />
          </Space>
        </Col>
        <Col
          span={4}
          style={{
            display: 'flex',
            justifyContent: 'flex-end'
          }}
        >
          <Space size={0}>
            <Button
              shape='circle'
              type='text'
              icon={<SearchOutlined className='dms-table-icon-secandary' />}
              size={'large'}
            />
            <Button
              shape='circle'
              type='text'
              icon={<PrinterOutlined className='dms-table-icon-secandary' />}
              size={'large'}
            />{' '}
            <Button
              shape='circle'
              type='text'
              icon={<DownloadOutlined className='dms-table-icon-secandary' />}
              size={'large'}
            />{' '}
            <Button
              shape='circle'
              type='text'
              icon={<FilterOutlined className='dms-table-icon-secandary' />}
              size={'large'}
            />
            <Button
              shape='circle'
              type='text'
              icon={<AppstoreAddOutlined className='dms-table-icon-primary' />}
              size={'large'}
            />
          </Space>{' '}
        </Col>
      </Row>
    )
  }
}
