import React from 'react'
// import 'antd/dist/antd.css'
// import './stylep.css'
import { Table } from '../../core/index'
import TitleCustomize from './TitleCustomize'

export default class DataTable extends React.Component {
  render() {
    return (
      <div>
        <Table
          className='dms-table'
          title={() => <TitleCustomize vues={this.props.vues} />}
          {...this.props}
        />
      </div>
    )
  }
}
